<template>
<div>
    <div class="miniheader">
        <div class="container">
            <div class="miniheader-logo"><router-link to="/" type="a">小米官网</router-link></div>
            <div class="miniheader-title ">
                <h2>确认订单</h2>
                <p>温馨提示：产品是否购买成功，以最终下单为准哦，请尽快结算</p>

            </div>
            <div class="miniheader-info">
                <i class="iconfont">&#x221a;</i>
                <a href="#" class="user-name"><span></span></a>
                <span class="sep">|</span>
                <a href="#" class="info-order">我的订单</a>
            </div>
        </div>
    </div>

    <div class="checkout">
        <div class="container">
            <div class="checkout-box">
                <div class="checkout-address clearfix">
                    <p class="address-header">收货地址</p>
                    <div class="address-body">
                        <!-- 有select时候才会修改字样 -->
                        <div class="address-item" v-for="(v,k) in userAddMs" :id="v.address_id" :class="{select:v.address_id===defaultAd}" @click="changeSelect(v)">
                            <dl>
                                <dt class="recipient">{{v.recipient}}</dt>
                                <dd class="phone">{{v.phone}}</dd>
                                <dd class="area">{{v.area}}</dd>
                                <dd class="address">{{v.address}}</dd>
                            </dl>
                            <div class="actions"><a href="javascript:;" @click="changeUserAd(v)">修改</a></div>
                        </div>

                        <div class="address-item address-new">
                            <i class="iconfont" @click="addNewAd">&#xe609;</i>
                            添加新地址
                        </div>
                    </div>
                </div>
                <div class="checkout-goods">
                    <p class="goods-header">商品</p>
                    <div class="goods-body">
                        <ul class="goods-list clearfix">
                            <li class="clearfix" v-for="(v,k) in selectedShop" :key="v.id">
                                <div class="goods-img"><img src="" alt=""></div>
                                <div class="goods-name">{{v.name}}</div>
                                <div class="goods-price">{{v.price}}元x{{v.quantity}}个</div>
                                <div class="goods-total">{{v.price*v.quantity}}元</div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="checkout-count clearfix">
                    <div class="count-box">
                        <ul>
                            <li>
                                <label>商品件数：</label>
                                <span><span class="total-quantity">{{totalNum}}</span>件</span>
                            </li>
                            <li>
                                <label>商品总价：</label>
                                <span class="total-price"><span class="price">{{totalPrice}}</span>元</span>
                            </li>
                            <li>
                                <label>活动优惠：</label>
                                <span>-0元</span>
                            </li>
                            <li>
                                <label>优惠券抵扣：</label>
                                <span>-0元</span>
                            </li>
                            <li>
                                <label>运费：</label>
                                <span>0元</span>
                            </li>
                            <li class="total-price">
                                <label>应付总额：</label>
                                <span><em class="price">{{totalPrice}}</em>元</span>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="checkout-bar clearfix">
                    <div class="bar-btn"><a href="javascript:;" id="checkout" @click="checkedOut">去结算</a></div>
                </div>
            </div>
        </div>
    </div>


    <div class="edit-bg" v-show="isOpenModal"></div>
    <div class="edit-address" v-show="isOpenModal">
        <div class="edit-header">
            <span class="title">添加收货地址</span>
            <a href="javascript:;" class="close" @click="isOpenModal=false"><i class="iconfont">&#xe602;</i></a>
        </div>
        <div class="edit-body">
            <form class="body-form clearfix">
                <input type="hidden" name="address_id">
                <div class="form-section form-name">
                    <!-- <label>收件人</label> -->
                    <input type="text"
                           name="recipient"
                           class="input-text name-input"
                           placeholder="收货人姓名"
                           v-model="defaultUserAdd.recipient"
                           v-validate="'required'"
                           data-vv-as="收货人姓名"
                           :class="{errBorder:errors.has('recipient')}"
                    >
                    <div><span class="error">{{ errors.first('recipient') }}</span></div>
                </div>
                <div class="form-section form-phone">
                    <!-- <label>手机号</label> -->
                    <input
                            type="text"
                            name="phone"
                            class="input-text phone-input"
                            v-model="defaultUserAdd.phone"
                            placeholder="手机号"
                            v-validate="'required'"
                            data-vv-as="手机号"
                            :class="{errBorder:errors.has('phone')}"
                    >
                    <div><span class="error">{{ errors.first('phone') }}</span></div>
                </div>
                <div class="form-section form-area">
                    <input
                            type="text"
                            name="area"
                            class="input-text area-input"
                            v-model="defaultUserAdd.address"
                            placeholder="选择省 / 市 / 区 / 街道"
                            v-validate="'required'"
                            data-vv-as="选择省 / 市 / 区 / 街道"
                            :class="{errBorder:errors.has('area')}"
                    >
                    <div><span class="error">{{ errors.first('area') }}</span></div>
                </div>
                <div class="form-section form-detail">
                    <!-- <label>详细地址</label> -->
                    <textarea
                            type="textarea"
                            name="address"
                            class="input-text detail-input"
                            placeholder="详细地址，路名或街道名称，门牌号"
                            v-model="defaultUserAdd.area"
                            v-validate="'required'"
                            data-vv-as="详细地址，路名或街道名称，门牌号"
                            :class="{errBorder:errors.has('address')}"
                    > </textarea>
                    <div><span class="error">{{ errors.first('address') }}</span></div>
                </div>
            </form>

        </div>
        <div class="edit-footer">
            <button href="javascript:;"
               class="btn btn-primary confirm"
               @click="confirmUpdate"
               :disabled="(errors.has('address')||errors.has('area')||errors.has('phone')||errors.has('recipient'))?true:false"

            >保存</button>
            <a href="javascript:;" class="btn btn-gray close" @click="isOpenModal=false">取消</a>
        </div>
    </div>
    {{selectedId}}
    {{defaultUserAdd}}
</div>
</template>
<script>
import {api} from "../utils/api"
import {mapState} from "vuex"
export default{
    created(){
       this.getAdds().then(()=>{
          this.defaultAd=this.userAddMs[0].address_id;
          for(var i=0;i<this.userAddMs.length;i++){
              if(this.userAddMs[i].address_id===this.defaultAd){
                  this.defaultUserAdd.recipient=this.userAddMs[i].recipient;
                  this.defaultUserAdd.address_id=this.userAddMs[i].address_id;
                  this.defaultUserAdd.phone=this.userAddMs[i].phone;
                  this.defaultUserAdd.area=this.userAddMs[i].area;
                  this.defaultUserAdd.address=this.userAddMs[i].address;
              }
          }//初始化默认用户地址

       });
       this.selectedId=this.$route.query.ids.split(',');
        for(let j=0;j< this.shopCartContent.length;j++){
         for(let i=0;i< this.selectedId.length;i++){
             if(this.selectedId[i]===this.shopCartContent[j].id){
                 this.selectedShop.push(this.shopCartContent[j])
             }
         }
       }
        this.selectedShop.forEach(v=>{
            this.totalNum+=(+v.quantity);
            this.totalPrice+=(+v.quantity*v.price)
        });

    },
    computed:{
        ...mapState("userMs",["shopCartContent"]),
    },
    data(){
        return{
            userAddMs:[],
            selectedShop:[],
            selectedId:[],
            //计算价钱和总商品
            totalNum:0,
            totalPrice:0,
            //用户地址相关
            defaultAd:0,
            isOpenModal:false,//默认模态框状态
            defaultUserAdd:{
                address_id:'',
                recipient:'',
                phone:0,
                address:'',
                area:'',
            }
        }
    },
    methods:{
        getAdds(){
           return api.get("/user/addressList").then(response=>{
                this.userAddMs=response;
            });
        },
        addNewAd(){
            this.isOpenModal=true;
            this.defaultUserAdd.address_id=''
        },
        changeSelect(val){
            this.defaultAd=val.address_id;
            this.defaultUserAdd.address_id=val.address_id;
            this.defaultUserAdd.recipient=val.recipient;
            this.defaultUserAdd.phone=val.phone;
            this.defaultUserAdd.area=val.area;
            this.defaultUserAdd.address=val.address;
        },
        confirmUpdate(){
            this.isOpenModal=false;
            api.post("/user/updateAddress",this.defaultUserAdd).then(()=>{
                this.getAdds();
                this.defaultUserAdd.address_id=this.defaultAd;
            })
        },
        changeUserAd(val){//选择框用户地址
            this.isOpenModal=true;
            this.defaultUserAdd.address_id=val.address_id;
            this.defaultUserAdd.recipient=val.recipient;
            this.defaultUserAdd.phone=val.phone;
            this.defaultUserAdd.address=val.address;
            this.defaultUserAdd.area=val.area;
        },
        checkedOut(){
            let ms={
                selectShopMs:this.selectedShop,
                userAdMs:this.defaultUserAdd
            };
            let data={
                ids:'',
                address_id:'',
            };
            data.address_id=this.defaultUserAdd.address_id;
            data.ids=this.selectedId.toString();
            this.$store.dispatch("selectComfirm/disChangeMs",ms);
            this.$store.dispatch("userOrder_no/getOrder_no",data).then(()=>{
                let order_no=this.$store.state.userOrder_no.order_no;
                this.$router.replace({ path: '/confirm', query: { data:order_no }})
            });


        }
    }
}
</script>
<style>
    @import "../css/style.css";
    @import "../css/checkout.css";
    @import "../css/iconfont.css";
    .error{
        color: red;
    }
</style>